.myAccountContent {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background-color: #f9f9f9;
  border-radius: 5px;
  padding: 25px 40px 20px 40px;
  min-width: 772px;
  width: 60%;
  height: 70%;

}

.infoContainer {
  /*serkan old none*/
  height: 100%;
}


.userInfo {
  display: flex;
  align-items: center;
  flex-direction: row;
  margin-bottom: 15px;
  height: 24px;
  flex-shrink: 0;
}

.userImage {
  width: 100px;
  height: 100px;
  border-radius: 50px;
  margin-right: 20px;
}

.userName {
  font-size: 20px;
  display: flex;
}

.userInformationRow {
  margin-bottom: 20px;
}

.userNameBold {
  font-size: 20px;
  font-weight: 600;
  color: #4e617e;
  margin-left: 5px;
}

.userCredits {
  font-size: 16px;
}

.creditsButton {
  background-color: #4e617e;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  width: 70px;
  height: 30px;
  font-size: 10px;
  margin-left: 10px;
}

.tabLayoutContainer {
  display: flex;
  flex-direction: column;
  /* height: 250px; */
}

.tabsContainer {
  border-bottom: 1px solid #a9a9ac;
  height: 50px;
  flex-shrink: 0;
}

.summaryHistoryItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  padding: 10px;
  height: 50px;
  margin-top: 15px;
  border-width: 1px;
  border-color: #4e617e;
  border-radius: 5px;
  border-style: solid;
  cursor: pointer;
}

.summaryHistoryTitle {
  white-space: nowrap;
  /* Metni tek bir satırda tutar */
  overflow: hidden;
  /* İçeriğin dışarı taşmasını engeller */
  text-overflow: ellipsis;
  /* Kesilen metnin sonuna üç nokta ekler */
  width: 80%;
  /* Gerekirse genişliği ayarlayabilirsiniz */
  display: block;
  font-size: 18px;
  color: #4e617e;
  font-weight: 500;
}

.summaryDate {
  font-size: 14px;
  color: #000;
  font-weight: 200;
}

.settingsItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  padding: 10px;
  margin-top: 15px;
  border-width: 1px;
  border-color: #4e617e;
  border-style: solid;
  border-radius: 5px;
}

.settingsItemTitleContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.settingsItemTitle {
  flex: 1;
  margin-right: 20px;
  font-size: 18px;
  color: #4e617e;
  font-weight: 500;
}

.settingsItemDescription {
  font-size: 11px;
  color: #a9a9ac;
  font-weight: 500;
}

.settingsItemActionDescription {
  font-size: 13px;
  color: #4e617e;
  font-weight: 500;
}

.history-list {
  height: 100%;
  overflow-y: auto;
  padding-right: 5px;
  /* position: relative; */

}

.historyList2 {
  /*serkan old none*/
  height: 323px;
}



.history-list::-webkit-scrollbar {
  width: 10px;
  /* Scrollbar genişliği */
}

.history-list::-webkit-scrollbar-track {
  background: #F9F9F9;
  /* Scrollbar track (arka plan) rengi */
}

.history-list::-webkit-scrollbar-thumb {
  background: #CCCCCC;
  /* Scrollbar thumb (hareketli kısım) rengi */
  border-radius: 5px;
}

.history-list::-webkit-scrollbar-thumb:hover {
  background: #B0B0B0;
  /* Scrollbar thumb üzerine gelindiğinde rengi */
}

/* Firefox için scrollbar stilleri */
.history-list {
  scrollbar-width: thin;
  /* "auto" veya "thin" */
  scrollbar-color: #CCCCCC #F9F9F9;
  /* thumb ve track renkleri */
}

.auth_success_container {
  width: 800px;
  height: 570px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(231, 231, 231, 1);
  border-radius: 20px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  padding: 50px 100px 50px 100px;
}

.auth-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, .5);
  backdrop-filter: blur(5px);
}

.auth_header {
  color: #000;
  font-size: 26px;
  font-family: 'Exo 2';
  font-weight: 700;
  text-transform: uppercase;
  word-wrap: break-word;
}

.auth_content {
  color: #000;
  font-size: 20px;
  font-family: 'Exo 2';
  font-weight: 700;
  word-wrap: break-word;
}

.auth_header_container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;

}

.auth_icon {
  width: 42px;
  height: 53px;
  margin-right: 10px;
  color: #00AC11;
}

.auth_images_container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.auth_button {
  width: 189;
  height: 53;
  background: #00AC11;
  color: #e7e7e7;
  font-size: 26px;
  border: none;
  border-radius: 6px;
  padding: 10px 25px;
  overflow: hidden;
  margin: 0 auto;
  cursor: pointer;
  transition: all .1s ease-in-out;

  &:hover {
    scale: 1.05;

  }

  &:active {
    scale: 1;
  }

}

.myAccountContainer {
  position: relative;
}

.email-verify-warning {
  position: absolute;
  top: 30px;

  max-width: 70%;
}

.email-verify-show {
  display: block !important;
}

.email-verify-hide {
  display: none !important;
}